import React from "react"
import loadable from '@loadable/component';

import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

const Slider = loadable(() => import('react-slick'));

const HomeTestimony = ({ testimonials }) => {
  var testimonySettings = {
    slidesToShow: 1,
    slidesToScroll: 1,
    infinite: true,
    fade: true,
    arrows: false,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 5000,
    cssEase: "linear",
  }
  return (
    <div className="home_testimony_block">
      <div className="container">
        <h3 className="animate__animated animate__fadeInUp">Reviews</h3>
        <div className="home_testimony_area">
          <Slider {...testimonySettings}>
            {testimonials.length > 0 &&
              testimonials.map((item, index) => {
                return (
                  <div className="home_testimony_box animate__animated animate__fadeInRight" key={index}>
                    <h4>{item.title}</h4>
                    <div dangerouslySetInnerHTML={{ __html: item.content }} />
                  </div>
                )
              })}
          </Slider>
        </div>
      </div>
    </div>
  )
}

export default HomeTestimony
