import React from "react"
import Slider from "react-slick"

import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

const HomeBlog = ({ blogs }) => {
  var blogSettings = {
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    infinite: false,
    responsive: [
      {
        breakpoint: 993,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 500,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  }
  return (
    <div className="home_blog_block">
      <div className="container">
        <h2 className="animate__animated animate__slideInUp">Blog</h2>
        <div className="home_blog_area">
          <Slider {...blogSettings}>
            {blogs.length > 0 &&
              blogs.map((item, index) => {
                return (
                  <div className="home_blog_box animate__animated animate__flipInY" key={index}>
                    <div className="home_blog_thumb">
                      <img
                        src={item.featuredImage.node.sourceUrl || ""}
                        width={365}
                        height={250}
                        loading="eager"
                        alt={item.featuredImage.node.altText || "Propey"}
                      />
                    </div>
                    <p>{item.title}</p>
                  </div>
                )
              })}
          </Slider>
        </div>
      </div>
    </div>
  )
}

export default HomeBlog
