import React from "react"

const ContactExpert = ({ html }) => {
  return (
    <div className="expert_block">
      <div className="container">
        <div
          className="expert_area animate__animated animate__slideInUp"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </div>
    </div>
  )
}

export default ContactExpert
