import React from "react"

const HomeServices = ({ services }) => {
  return (
    <div className="home_service_block">
      <div className="container">
        <div className="home_service_area">
          <div className="home_service_left">
            <div className="container">
              <div
                className="animate__animated animate__fadeInRight"
                dangerouslySetInnerHTML={{ __html: services.servicesContent }}
              />
            </div>
          </div>
          <div className="home_service_right">
            <div className="container">
              <div className="home_service_grid">
                {services.services.length > 0 &&
                  services.services.map((item, index) => {
                    return (
                      <div className="home_service_box animate__animated animate__fadeInRight" key={index}>
                        <img
                          src={item.serviceIcon?.node?.sourceUrl}
                          alt={item.serviceIcon?.node?.altText || "Propey"}
                          width={70}
                          height={70}
                        />
                        <p>{item.service}</p>
                      </div>
                    )
                  })}
              </div>
              <div
                className="animate__animated animate__fadeInRight"
                dangerouslySetInnerHTML={{ __html: services.otherServices }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default HomeServices
