import React, { useEffect, useState } from "react"

const CounterItem = ({ endValue, additionalSymbol, content }) => {
  const [count, setCount] = useState(0)

  useEffect(() => {
    const totalDuration = 1000 // Total duration of the animation in milliseconds (3 seconds)
    const intervalTime = 100 // Update interval in milliseconds
    const numberOfSteps = totalDuration / intervalTime // Total number of increments
    const increment = Math.ceil(endValue / numberOfSteps)

    const interval = setInterval(() => {
      setCount(prevCount => {
        const newCount = prevCount + increment
        if (newCount >= endValue) {
          clearInterval(interval)
          return endValue
        }
        return newCount
      })
    }, intervalTime) // Update the counter every 100 milliseconds

    return () => clearInterval(interval) // Cleanup interval on component unmount
  }, [endValue])

  return (
    <div className="home_count_box animate__animated animate__flipInY">
      <span>
        {count}
        {additionalSymbol}
      </span>
      <p>{content}</p>
    </div>
  )
}

export default CounterItem
