import React from "react"
import Slider from "react-slick"

import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

const HomeGuide = ({ guide }) => {
  var guideSettings = {
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    infinite: false,
  }
  return (
    <div className="home_guide_block">
      <div className="container">
        <Slider {...guideSettings}>
          {guide.length > 0 &&
            guide.map((item, index) => {
              return (
                <div className="home_guide_area animate__animated animate__slideInRight" key={index}>
                  <div
                    dangerouslySetInnerHTML={{ __html: item.guideContent }}
                  />
                </div>
              )
            })}
        </Slider>
      </div>
    </div>
  )
}

export default HomeGuide
