import React from "react"
import CounterItem from "./counter"

const HomeCounter = ({ counter }) => {
  return (
    <div className="home_count_block">
      <div className="container">
        <h2 className="animate__animated animate__slideInUp">{counter.heading}</h2>
        <div className="home_count_area">
          {counter.counters.length > 0 &&
            counter.counters.map((item, index) => {
              return (
                <CounterItem
                  key={index}
                  endValue={item.count}
                  additionalSymbol={item.additionalSymbol}
                  content={item.content}
                />
              )
            })}
        </div>
      </div>
    </div>
  )
}

export default HomeCounter
